<template>
    <div class="  " style="">
        <!-- <div class="  " style="overflow: auto;" @scroll="scrollEvent"> -->
        <div class="  " >
            <div class="flexBetween">
                <div class="f18">评论</div>
                <div class="">
                    <div class="menus ml10 alignCenter" style="flex:1;">
                        <!-- <div class="mr5 cloa f12">市场</div> -->
                        <div v-for="(v,o) in typeList" :key="o" class="menu" :class="{'active':active==o}" @click="active=o;">{{v.text}}</div>
                    </div>
                </div>
            </div>
            <div class="mt10 bg0 br8 p15">
                <textarea v-model="form.feedback" rows="2" class="textareaStyle" style="width:100%;" maxLength="1000" :placeholder="$t('发表您的评论...')"></textarea>
            </div>
            <div class="mt10">
                <div v-for="(v, o) in list" :key="o" class="list flexBox">
                    <div class="">
                        <img :src='require("@/assets/img/user.png")' class="" style="width:30px;height:30px;" />
                    </div>
                    <div class="ml10 pb5" style="flex:1;border-bottom: 1px solid #1b1d22;">
                        <div class="flexBox" style="line-height: 30px;">
                            <span class="text_deal" style="flex:1;">{{ v.nickname }}</span>
                            <img :src='require("@/assets/img/reply.svg")' @click="form.parentId = v.userId; isSendPop = true;"
                                class="active_t" style="width:18px;height:18px;" />
                        </div>
                        <div class="mt5 text_deal">{{ v.content || '没有内容...' }}</div>
                        <div class="mt5">{{ v.time || '没有内容...' }}</div>
                    </div>
                </div>
                <noData v-if="!page.loading" :list="list" style="flex-shrink: 0; width: 100%;"></noData>
                <loading v-if="page.loading"></loading>
            </div>
        </div>
        <div v-if="isSendPop" class="sendPop">
            <div style="">
                <Input v-model="form.content" placeholder="说点什么呢..." type="textarea" :rows="3" class=""
                    style="width:60%;height:100%;"></Input>
            </div>
            <div class="mt10 pb10">
                <Button type="primary" style="min-width:100px;" @click="submit">提交</Button>
                <Button style="margin-left: 15px;min-width:100px;" @click="isSendPop = false">关闭</Button>
            </div>
        </div>
        <!-- <div v-if="!isSendPop" class="sendBtn">
            <Button type="primary" style="min-width:80px;" @click="form.parentId = ''; isSendPop = true;">发文</Button>
        </div> -->
    </div>
</template>
    
<script>
export default {
    name: '',
    props: {
        area: {
            type: Object,
            default: () => { }
        }
    },
    components: {

    },
    data() {
        return {
            list: [],
            page: {
                "current": 1,
                "size": 20,
                loading: false,
                finish: false,
            },
            form: {
                content: '',
                parentId: '',
            },
            isSendPop: false,
            typeList:[{text:'最新'},{text:'最热'}],
            active:0,
        }
    },
    mounted() {
        this.getList();
    },
    methods: {
        //重新初始化分页
        init() {
            this.page = {
                "current": 1,
                "size": 20,
                loading: false,
                finish: false,
            }
            this.list = [];
            this.getList();
        },
        //发送
        submit() {
            if (!this.form.content) {
                this.$Message.error('请输入内容!');
                return;
            }
            let d = {
                area: this.area.type,
                "content": this.form.content,
                "parentId": this.form.parentId,
            }
            this.$api.trading_view_comment(d).then(res => {
                this.$Message.success('发送成功!');
                this.form.content = ''
                this.isSendPop = false;
                this.init();
            });
        },
        // 滚定监听
        scrollEvent(e) {
            let that = this;
            if (Math.ceil(e.currentTarget.scrollTop + e.currentTarget.clientHeight) >= e.currentTarget.scrollHeight - 20) {   //容差：20px
                console.log('滚动到底部');
                if (!that.page.finish && !that.page.loading) {
                    that.page.current += 1;
                    that.getList();
                }
            }
        },
        //列表
        async getList() {
            if (this.page.loading) {
                return;
            }
            let d = {
                // area: this.area.type,
                area: 'shenzhen',
                current: this.page.current,
                size: this.page.size,
            };
            this.page.loading = true;
            // let res = await this.$api.trading_view_comment(d);
            let res = await this.$api.trading_view_com_page(d);
            let list = res.list || [];
            if (list.length >= d.size) {
                this.page.finish = false;
            } else {
                this.page.finish = true;
            }
            if (d.current == 1) {
                this.list = list;
            } else {
                this.list = this.list.concat(list);
            }
            this.page.loading = false;
        },
    },
    watch: {
        'area': {
            handler: function (val, value) {
                console.log('修改')
                this.init();
            },
            deep: true,
            immediate: false
        }
    }
}
</script>
    
<style scoped lang='less'>
