    // cursor: pointer;
    padding: 5px 12px;
    // border-bottom: 1px solid #434651;
    // border-bottom: 1px solid #2d2f37;
    position: relative;
}

.sendPop {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px;
    box-sizing: border-box;
    background: #17171a;
}

.sendBtn {
    position: absolute;
    bottom: 10px;
    right: 10px;
}

