<template>
    <div class="block">
        <!-- <div class="secondMenus">
            <div v-for="(v, o) in categoryList" :key="o" class="secondMenu" :class="{ 'active': active == o }"
                @click="changeType(v, o)">{{ v }}</div>
        </div> -->
        <div class=" p15" style="background-color: rgb(30, 32, 38);border-radius: 8px;">
            <div class="f18 clof p5_0">相关资讯</div>
            <div class=" f16">
                <div v-for="(v, o) in list" :key="o" @click="toDetail(v)" class="dataLIst cloc7">
                    <div class="text_deal">{{ v.title }}</div>
                    <div class="alignCenter f12 mt5 op7">
                        <div class="text_deal" style="flex:1;">
                            <span>{{ v.source }}</span>
                            <!-- <span class="ml10">#重要舆论#</span> -->
                        </div>
                        <div class="ml10">{{ v.resourceTime | getDateDiff }}</div>
                    </div>
                </div>
            </div>
            
        </div>
        <!-- <div class="mt20">
            <Page :total="page.totalCount" :current="page.current" @on-change="handlePage" show-total />
        </div> -->
    </div>
</template>

<script>
export default {
    name: 'WebNews',
    data() {
        return {
            categoryList:['全部'],
            active:0,
            list:[],
            page:{
                "type": '',
                "current": 1,
                "size": 5,
                totalCount:0,
            },
        };
    },
    mounted() {
        this.getList();
        this.getType();
    },
    methods: {
        changeType(v,o){
            this.active=o;
            this.page.type=v;
            this.page.current=1;
            this.list=[];
            this.getList();
        },
        handlePage(page) {
            this.page.current = page;
            this.getList();
        },
        //分类
        getType(){
            this.$api.news_typs().then(res=>{
                let arr = res || [];
                this.categoryList=this.categoryList.concat(arr);
            });
        },
        toDetail(v){
            localStorage.setItem('information_detail',JSON.stringify(v));
            // this.$router.replace(`/informationDetail`);
            window.open('/informationDetail','_blank');
        },
        //根据接口获取
        async getList(){
            let d={
                "type": this.page.type=='全部'?'':this.page.type,
                "current": this.page.current,
                "size": this.page.size,
            };
            let res = await this.$api.news_page(d);
            let list = res.list || [];
            this.list=list;
            this.page.totalCount= res.totalCount;
        },
    },
};
</script>

<style lang="less" scoped>
.dataLIst{
    padding:10px 5px;
}
.dataLIst:hover{
    background: rgba(56, 97, 251, 0.1);
}
</style>